
import {
    FETCH_RENTALS_BY_ID_SUCCESS, FETCH_RENTALS_BY_ID_INIT, FETCH_RENTALS_SUCCESS,
    FETCH_RENTALS_INIT,
    FETCH_RENTALS_FAIL
} from '../actions/types';

const INITIAL_STATE = {
    rentals: {
        data: [],
        errors: []
    },
    rental: {
        data: {}
    }
}
export const rentalReducer = (state = INITIAL_STATE.rentals, action) => {

    switch (action.type) {
        case FETCH_RENTALS_SUCCESS: {
            return { ...state, data: action.rental }
        }
        case FETCH_RENTALS_FAIL: { return Object.assign({}, state, { errors: action.errors, data: [] }); }
        case FETCH_RENTALS_INIT: return { ...state, data: [], errors: [] };

        default:
            return state
    }
}

export const rentalDetailReducer = (state = INITIAL_STATE.rental, action) => {

    switch (action.type) {
        case FETCH_RENTALS_BY_ID_INIT: {
            return { ...state, data: {} }
        }
        case FETCH_RENTALS_BY_ID_SUCCESS: {
            return { ...state, data: action.rental }
        }
        default:
            return state
    }
}